.projects-section {
	background-color: rgb(235, 235, 235);
	height: 80vh;
}

.projects-container {
	width: 80%;
	height: 80%;

	display: grid;
	grid-gap: 1rem;
	background: none;
}

.projects-card {
	width: 25%;
	aspect-ratio: 16 / 9;
	box-shadow: 0px 1px 20px 0 rgba(0, 0, 0, .4);
	overflow: hidden;
	background-color: white;
	border-radius: 10px;
	transition: transform 0.2s;
}

.projects-card:hover {
	transform: scale(1.03);
}

.projects-image {
	min-width: 100%;
	max-height: 100%;
	object-fit: cover;
	object-position: center;
	transition: transform 0.2s;
}

.projects-image:hover {
	transform: scale(1.1);
}

.projects-label {
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .8);
	display: flex;

	position: absolute;
	top: 0px;

	flex-direction: column;
	justify-content: top;
	align-items: center;

	padding-top: 16px;
	padding-bottom: 16px;

	opacity: 0;
	transition: opacity 0.2s;
}

.projects-label:hover {
	opacity: 1;
}

.projects-label h1 {
	color: white;
	font-size: 26px;
	font-weight: 900;
	width: 100%;
	text-align: center;
}

.projects-label h2 {
	color: white;
	font-size: 18px;
	font-weight: 100;
	width: 100%;
	text-align: center;
}