.contact-section {
	background: linear-gradient(rgba(83, 83, 83, 0.336), rgba(83, 83, 83, 0.336)), url(../../../public/background.png);
	height: 80vh;
}

.contact-section h1 {
	color: white;
	font-size: 46px;
	font-weight: bold;
	text-shadow: 2px 2px rgba(0, 0, 0, .4);
}

.contact-section h2 {
	color: white;
	font-size: 22px;
	font-weight: lighter;
	text-shadow: 2px 2px rgba(0, 0, 0, .4);
}