* {
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
	font-family: 'Roboto', sans-serif;
}

html, body {
	max-width: 100vw;
	overflow-x: hidden;
}

.navbar {
	width: 95%;
	height: 52px;
	display: flex;
  	align-items: center;
	position: fixed;
	padding-left: 5%;
	top: 0px;
	transition: all 0.15s;
	transition-timing-function: linear;
}

.navbar-title {
	color: white;
	text-align: left;
	font-size: 26px;
	font-weight: 700;
}

.navbar-button-container {
	height: 52px;
	width: 80rem;
	right: 52px;
	position: absolute;
	display: flex;
	align-items: center;
	vertical-align: middle;
	justify-content: right;
}

.navbar-button-container button {
	width: 78px;
	height: 100%;
	font-size: 18px;
	font-weight: 100;
	color: lightgray;
	background: none;
	margin: 0px 5px;
	opacity: 0.5;
	border: none;
	border-bottom: solid;
	border-bottom-width: 0px;
	transition: all 0.2s;
}

.navbar-button-container button:hover {
	color: white;
	opacity: 1;
	border-bottom-width: 2px;
}

.section {
	width: 100%;
	/*height: 80vh;*/
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}